                    <li>Jeux vidéo</li>\n                    <li>Montage vidéo</li>\n                    <li>Informatique</li>\n                </ul>\n            </div>\n\n            <div class=\"section education\">\n                <h2>Formations</h2>\n                <ul>\n                    <li>2020 - 2023 : Baccalauréat Professionnel Technicien Conseil et Vente en Univers Jardin : Mention Assez Bien</li>\n                    <li>2023 - 2025 : BTS Service Informatique aux Organisations : En cours - 2nd année</li>\n                </ul>\n            </div>\n\n            <div class=\"section experiences\">\n                <h2>Expériences</h2>\n                <ul>\n                    <li><strong>2021 - 2022 :</strong> Stage Conseiller Vendeur - Jardil Éclore (7 semaines)</li>\n                    <li><strong>2022 - 2023 :</strong> Employé Polyvalent - Gamme Vert</li>\n                    <li><strong>2023 - 2024 :</strong> Stage de fin d'année, Service Informatique - Planfor (5 semaines)</li>\n                    <li><strong>2023 - 2024 :</strong> Job d'été - Lidl (Juillet/Août)</li>\n                </ul>\n            </div>\n\n            <div class=\"section skills\">\n                <h2>Compétences</h2>\n                <ul>\n                    <li><strong>Langues :</strong> Anglais</li>\n                    <li><strong>Logiciels maîtrisés :</strong> Visual Studio Code, Spyder (Python), NetBeans, Canva</li>\n                    <li><strong>Langages en apprentissage :</strong> PHP, Java, Android</li>\n                </ul>\n            </div>\n        </div>\n    </div>\n</template>\n\n<style scoped>\n    .cv-container {\n        font-family: 'Arial', sans-serif;\n        max-width: 1000px;\n        margin: 0 auto;\n        padding: 20px;\n        background-color: #f4f4f4;\n        border-radius: 8px;\n    }\n\n    .header {\n        text-align: center;\n        margin-bottom: 30px;\n        background-color: #333;\n        color: white;\n        padding: 20px;\n        border-radius: 8px;\n    }\n\n    .header h1 {\n        margin: 0;\n        font-size: 36px;\n        font-weight: bold;\n    }\n\n    .header p {\n        font-size: 18px;\n        margin: 0;\n    }\n\n    .button-container {\n        display: flex;\n        justify-content: center;\n        gap: 15px;\n        margin-top: 20px;\n    }\n\n    .download-btn, .view-btn {\n        padding: 10px 20px;\n        background-color: #3498db;\n        color: white;\n        border: none;\n        border-radius: 5px;\n        cursor: pointer;\n        font-size: 16px;\n    }\n\n    .download-btn:hover, .view-btn:hover {\n        background-color: #2980b9;\n    }\n\n    .view-btn {\n        background-color: #2ecc71;\n    }\n\n    .view-btn:hover {\n        background-color: #27ae60;\n    }\n\n    .sections {\n        display: grid;\n        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n        gap: 20px;\n    }\n\n    .section {\n        background-color: white;\n        padding: 20px;\n        border-radius: 8px;\n        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n        transition: transform 0.3s ease, box-shadow 0.3s ease;\n    }\n\n    .section:hover {\n        transform: translateY(-5px);\n        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);\n    }\n\n    .section h2 {\n        font-size: 24px;\n        margin-bottom: 10px;\n        color: #333;\n        border-bottom: 2px solid #3498db;\n        padding-bottom: 5px;\n    }\n\n    .section p,\n    .section ul {\n        font-size: 16px;\n        color: #555;\n        line-height: 1.6;\n    }\n\n    .section ul {\n        list-style-type: disc;\n        padding-left: 20px;\n    }\n\n    .section ul li {\n        margin-bottom: 8px;\n    }\n\n    .section ul li strong {\n        font-weight: bold;\n        color: #3498db;\n    }\n</style>\n"],"sourceRoot":""}]);
